:root {
  --header-bg: #262626;
  --hero-bg: #f4f4f4;
  --nunito: "Nunito";
  --noto-sans: "Noto Sans";
  --white-color: #fff;
  --black-color: #000;
  --hover-color: #e1712b;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: var(--font-family);
  border: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  color: var(--white-color);
  line-height: 1;
  font-family: var(--nunito);
  font-weight: 400;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.header {
  background-color: var(--header-bg);
  justify-content: flex-start;
  align-items: center;
  padding: 25px 30px 25px 10px;
  display: flex;
}

.header-logo {
  width: 55px;
  height: auto;
  margin-right: auto;
}

.header-nav {
  gap: 35px;
  display: flex;
}

.header-link {
  color: var(--text-color);
  transition: color .4s;
  position: relative;
}

.header-link:after {
  content: "";
  background-color: var(--hover-color);
  width: 0;
  height: 2px;
  transition: width .3s, left .3s;
  position: absolute;
  bottom: -5px;
  left: 50%;
}

.header-link:hover {
  color: var(--hover-color);
}

.header-link:hover:after {
  width: 100%;
  left: 0;
}

.hero {
  background-image: url("hero-background.d52e3958.png");
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero-box {
  font-family: var(--noto-sans);
  background: #18181bb3;
  border-radius: 7px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 35px 25px;
  display: flex;
}

.hero-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 70px;
  font-weight: 500;
}

.hero-subtitle {
  font-size: 20px;
}

.intro {
  background-color: var(--hero-bg);
  color: var(--black-color);
  font-family: var(--noto-sans);
  text-align: center;
  padding-top: 40px;
  padding-bottom: 110px;
  font-weight: 500;
}

.intro-title {
  margin-bottom: 40px;
  font-size: 40px;
}

.intro-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 35px;
  display: flex;
}

.intro-card {
  background-color: var(--white-color);
  border-radius: 7px;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
  width: 800px;
  padding: 15px 10px;
  transition: transform .5s cubic-bezier(.175, .885, .32, 1.275);
  display: flex;
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

.intro-card:hover {
  transform: scale(1.05);
}

.icard-image {
  border-radius: 20px;
  width: 150px;
  height: 150px;
}

.icard-box {
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 7px;
  display: flex;
}

.icard-subtitle {
  color: #155dfc;
  margin-bottom: 20px;
}

.footer {
  background-color: var(--header-bg);
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}
